html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  position: relative;

  height: 100vh;
  margin: 0;
  padding: 0;

  font-family: "Circe", "Arial", sans-serif;
  font-size: 18px;
  line-height: 24px;
  font-weight: normal;
  color: #000000;

  overflow-x: hidden;

  &::-webkit-scrollbar {
    width: 0.55rem;
  }

  &::-webkit-scrollbar-track {
  	background-color: #f5f5f5;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgb(73,125,189);
    border-radius: 0.25rem;
  }
}

main {
  height: 100%;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  border: 0;

  clip: rect(0 0 0 0);
  overflow: hidden;
}
